
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Toast from 'vux/src/components/toast/index.vue'



const limitRange = [
{
name: '3-5万',
value: '3-5万',
parent: 0,
id: 1
},
{
name: '5-10万',
value: '5-10万',
parent: 0,
id: 2
},
{
name: '10-20万',
value: '10-20万',
parent: 0,
id: 3
},
{
name: '20万以上',
value: '20万以上',
parent: 0,
id: 4
}
]


export default {
components: {
XHeader,
Group,
XInput,
PopupPicker,
Toast
},
data () {
return {
id: this.$route.query.messageId,
limit_range: limitRange,
name: '',
mobile: '',
weixin: '',
addr: '',
invest: [],
investment_quota: 0
}
},
vuerify: {
name: {
test: /^[\u4E00-\u9FA5A-Za-z][\u4E00-\u9FA5A-Za-z0-9]+$/,
message: '联系人填写错误或未填写'
},
mobile: {
test: /^\d{11}$/,
message: '手机号填写错误或未填写'
}
},
methods: {
regTest() {
var $this = this;
if (!this.$vuerify.check()) { // 手动触发校验所有数据
this.$vux.toast.text('信息未填写完整', 'middle')
} else {




















for (let i in this.limit_range) {
if(this.limit_range[i].name == this.invest[0]) {
this.investment_quota = this.limit_range[i].id;
break;
}
}

var params = new URLSearchParams();
params.append('id', $this.id);
params.append('name', $this.name);
params.append('mobile', $this.mobile);
params.append('weixin', $this.weixin);
params.append('addr', $this.addr);
params.append('investment_quota', $this.investment_quota);
this.axios.post('http://dev.h5.xiaozhangbang.org/365/project/add_comment', params)
.then(function (response) {
console.log(response);
})
.catch(function (error) {
console.log(error);
});
}
}
}
}



