
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import comList from './../components/common/list'

export default {
components: {
XHeader,
Swiper,
SwiperItem,
comList
},
data () {
return {
img_list: [],
list_data: []
}
},
mounted () {
var $this = this;
this.axios.get('http://dev.h5.xiaozhangbang.org/365/project/get_banner_list')
.then(function (res) {

$this.img_list = res.data.data;
})
.catch(function (error) {
console.log(error);
});

this.axios.get('http://dev.h5.xiaozhangbang.org/365/project/get_project_list')
.then(function (res) {

$this.list_data = res.data.data;
})
.catch(function (error) {
console.log(error);
});
},
methods: {
toDetail(){
this.$router.push({
path: '/detail'
})
}
}
}



