
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'


export default {
components: {
XHeader,
Swiper,
SwiperItem,
Sticky
},
data () {
return {
banner_list: [],
detail_data: [],
id: this.$route.query.detailId
}
},
mounted() {
var $this = this;
this.axios.get('http://dev.h5.xiaozhangbang.org/365/project/show_detail_project?id='+$this.id)
.then(function (res) {
console.log(res);
$this.banner_list = res.data.data.banner;
$this.detail_data = res.data.data;
})
.catch(function (error) {
console.log(error);
});
},
methods: {
showInfo() {
console.log(this.id);
}
}
}



